/*stylelint-disable*/

html {
  -webkit-text-size-adjust: 100%;
}

hr {
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: inherit;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}

small {
  font-size: inherit;
}

sub,
sup {
  font-size: inherit;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

main,
nav,
footer,
header,
aside {
  display: block;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}

.container {
  max-width: 1770px;
  margin: 0 auto;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

html,
body {
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 0;
  line-height: 0;
  color: #000000;
  height: 100%;
  width: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

img {
  vertical-align: top;
  border-style: none;
  max-width: 100%;
  height: auto;
}

.wrapper {
  min-height: 100vh;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
}

.content {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
}

body.lock {
  overflow: hidden;
}

.main-header {
  width: 100%;
  min-height: 90px;
  padding-top: 71px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
}

.main-header .container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  position: relative;
}

.main-header__burger {
  display: block;
  width: 24px;
  height: 16px;
  padding: 0;
  position: relative;
  z-index: 4;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.main-header__burger span {
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  top: 0;
  background-color: transparent;
  transition: all 0.3s ease 0s;
}

.main-header__burger span::before {
  position: absolute;
  content: "";
  width: 2px;
  height: 2px;
  left: 0;
  top: 0;
  background-color: #000000;
  transition: all 0.3s ease 0s;
}

.main-header__burger span::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 2px;
  top: 0;
  right: 0;
  background-color: #f78e6a;
  transition: all 0.3s ease 0s;
}

.main-header__burger span:nth-child(2) {
  top: 7px;
}

.main-header__burger span:last-child {
  top: 14px;
}

.main-header__burger span:last-child::after {
  width: 14px;
  left: 4px;
}

.main-header__burger.active span:last-child {
  transform: scale(0);
}

.main-header__burger.active span::before {
  display: none;
}

.main-header__burger.active span:first-child::after {
  top: 7px;
  transform: rotate(-45deg);
}

.main-header__burger.active span:nth-child(2)::after {
  transform: rotate(45deg);
}

.main-header__burger:hover,
.main-header__burger:focus {
  outline: none;
  transition: all 0.3s ease 0s;
}

.main-header__burger:hover span::before,
.main-header__burger:focus span::before {
  background-color: #fc66a2;
  transition: all 0.3s ease 0s;
}

.main-header__burger:hover span::after,
.main-header__burger:focus span::after {
  background-color: #fc66a2;
  transition: all 0.3s ease 0s;
}

.main-header__burger:active span::before {
  background-color: rgba(247, 142, 106, 0.5);
  transition: all 0.1s ease 0s;
}

.main-header__burger:active span::after {
  background-color: rgba(252, 102, 162, 0.5);
  transition: all 0.1s ease 0s;
}

.main-header__link {
  display: block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
  color: #000000;
  text-decoration: none;
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0;
  position: fixed;
  top: 0;
  left: -100%;
  width: auto;
  height: 100%;
  overflow: auto;
  transition: all 0.3s ease 0s;
  padding: 90px 10px 20px 10px;
  z-index: 2;
}

.navigation.active {
  left: 50px;
  background-color: transparent;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navigation__list li {
  margin-bottom: 25px;
}

.navigation__list li:last-child {
  margin-right: 0;
}

.navigation__link {
  color: rgba(0, 0, 0, 0.5);
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  text-decoration: none;
  transition: all 0.2s ease 0s;
  position: relative;
}

.navigation__link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #f78e6a;
  bottom: 0;
  left: 0;
  transform: translateY(4px) scale(0);
}

.navigation__link:hover,
.navigation__link:focus {
  color: rgba(0, 0, 0, 0.9);
  transition: all 0.2s ease 0s;
  outline: none;
}

.navigation__link:active {
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.1s ease 0s;
}

.navigation__link.active {
  color: #000000;
}

.navigation__link.active:hover,
.navigation__link.active:focus {
  color: #000000;
}

.navigation__link.active:active {
  color: #000000;
}

.fullscreen {
  min-height: 100vh;
  display: flex;
  position: relative;
  padding-top: 70px;
  padding-bottom: 70px;
  background-color: #fbf5f4;
}

.fullscreen__body {
  flex: 1 1 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fullscreen__body::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  width: 49px;
  height: 30px;
  background-image: url("../img/logo.png");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.fullscreen__slogan {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding-left: 16px;
  font-size: 18px;
  line-height: 36px;
  font-weight: 400;
}

.fullscreen__slogan::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  background-color: #f78e6a;
  border-radius: 50%;
}

.fullscreen__row {
  display: flex;
  justify-content: center;
}

.fullscreen__col:last-child {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.fullscreen__img {
  display: block;
  width: 569px;
  height: 851px;
}

.fullscreen__head {
  position: relative;
  min-height: 145px;
}

.fullscreen__small {
  display: inline-block;
  position: absolute;
  left: -60px;
  top: 86px;
  transform: rotate(-90deg);
  font-size: 24px;
  font-weight: 400;
  line-height: 1;
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(to top, #f78e6a 0%, #fc66a2 100%);
}

.fullscreen__title {
  display: inline-block;
  margin-left: 10px;
  font-size: 200px;
  line-height: 1;
  font-weight: 500;
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(to top, #f78e6a 0%, #fc66a2 100%);
}

.fullscreen__text {
  max-width: 425px;
  padding-left: 90px;
  position: relative;
  font-size: 20px;
  line-height: 30px;
  font-weight: 300;
}

.fullscreen__text span {
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #f78e6a linear-gradient(to top, #f78e6a 0%, #fc66a2 100%);
}

.fullscreen__text span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 2px;
  background-color: #ffffff;
}

.fullscreen__text span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  width: 2px;
  height: 20px;
  background-color: #ffffff;
  transform: translate(-50%, -50%);
}

.fullscreen__next {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  padding-right: 40px;
  position: absolute;
  bottom: 4px;
  right: 0;
}

.fullscreen__next span {
  position: absolute;
  right: 0;
  top: 50%;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  transform: translateY(-50%);
  background: #f78e6a linear-gradient(to top, #f78e6a 0%, #fc66a2 100%);
}

.fullscreen__next span::before {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 6px solid #ffffff;
}

.fullscreen .social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-flex;
  position: absolute;
  bottom: 0;
  left: 15px;
}

.fullscreen .social li {
  margin-left: 15px;
  margin-right: 15px;
}

.fullscreen .social li:first-child {
  margin-left: 0;
}

.fullscreen .social li:last-child {
  margin-right: 0;
}

.fullscreen .social__link {
  display: block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  line-height: 24px;
  font-weight: 400;
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
}

.main-footer {
  background-color: transparent;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}

.social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-flex;
}

.social-list li {
  margin-left: 15px;
  margin-right: 15px;
}

.social-list li:first-child {
  margin-left: 0;
}

.social-list li:last-child {
  margin-right: 0;
}

.social-list__link {
  display: block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 10px;
  line-height: 24px;
  font-weight: 400;
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
}

@media (min-width: 879.98px) {
  .navigation__link:hover::after,
  .navigation__link:focus::after {
    transform: translateY(4px) scale(1, 1);
    transition: all 0.2s ease 0s;
  }

  .navigation__link:active::after {
    background-color: rgba(247, 142, 106, 0.3);
    transform: translateY(4px) scale(1, 1);
    transition: all 0.1s ease 0s;
  }

  .navigation__link.active:hover::after,
  .navigation__link.active:focus::after {
    display: none;
  }

  .navigation__link.active:active::after {
    display: none;
  }
}

@media (min-width: 1740px) {
  .fullscreen__img {
    margin-right: 111px;
  }
}

@media (max-width: 1189.98px) {
  .fullscreen__small {
    font-size: 18px;
    left: -44px;
    top: 64px;
  }

  .fullscreen__title {
    font-size: 152px;
  }
}

@media (max-width: 1039.98px) {
  .fullscreen__col:first-child {
    order: 2;
  }

  .fullscreen__col:last-child {
    order: 1;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 737px;
  }
}

@media (max-width: 375px) {
  .container {
    max-width: 365px;
  }
}